<template>
    <div class="sort-detail">
        <div class="header">
            <van-icon name="arrow-left" @click="routeBack" class="leftIcon" />
            <span class="detail-title">{{ major }}</span>
        </div>
        <ul class="tags">
            <TagItem
                class="tag"
                :class="{ active: tag.active }"
                v-for="(tag, index) in minor"
                :key="index"
                :item="tag.text"
                :activeIndex="index"
                @current-active="changeMinActive"
            ></TagItem>
        </ul>
        <ul class="tags">
            <TagItem
                class="tag"
                :class="{ active: tag.active }"
                v-for="(tag, index) in tags"
                :key="index"
                :item="tag.text"
                :activeIndex="index"
                @current-active="changeTagsActive"
            ></TagItem>
        </ul>
        <ul class="books" v-if="sortBookList.length != 0">
            <BookCard
                v-for="(book, index) in sortBookList"
                :key="index"
                :item="book"
            ></BookCard>
            <li class="more" @click="getMore">点击加载更多...</li>
        </ul>
        <div class="empty" v-else>无此类书籍</div>
    </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import types from "@/store/types.js";
import TagItem from "@/components/TagItem.vue";
import BookCard from "@/components/BookCard.vue";
import { getData } from "@/api/api.js";
export default {
    name: "SortDetail",
    components: {
        TagItem,
        BookCard,
    },
    data() {
        return {
            major: "",
            target: "",
            start: 1,
            limit: 20,
            minor: [],
            tags: [
                {
                    text: "最热",
                    active: true,
                    target: "hot",
                },
                {
                    text: "最新",
                    active: false,
                    target: "new",
                },
                {
                    text: "完结",
                    active: false,
                    target: "over",
                },
            ],
            selected: {
                minor: "",
                type: "hot",
            },
        };
    },
    computed: {
        ...mapState({
            sortBookList: (state) => state.sortBookList,
        }),
    },
    methods: {
        routeBack() {
            this.$router.back();
        },
        changeMinActive(i) {
            this.minor.forEach((el, index) => {
                el.active = false;
                if (index == i) {
                    el.active = true;
                    el.text == "全部"
                        ? (this.selected.minor = "")
                        : (this.selected.minor = el.text);
                }
            });
        },
        changeTagsActive(i) {
            this.tags.forEach((el, index) => {
                el.active = false;
                if (index == i) {
                    el.active = true;
                    this.selected.type = el.target;
                }
            });
        },
        ...mapActions([
            types.GET_SORT_DETAIL_BOOKS,
            types.GET_MORE_SORT_BOOKS,
            types.CLEAR_SORT_BOOKS,
        ]),
        changeSelected(val) {
            this[types.GET_SORT_DETAIL_BOOKS]({
                gender: this.target,
                type: val.type,
                major: this.major,
                minor: val.minor,
                start: this.start,
                limit: this.limit,
            });
        },
        getMore() {
            this.start += 1;
            this[types.GET_MORE_SORT_BOOKS]({
                gender: this.target,
                type: this.selected.type,
                major: this.major,
                minor: this.selected.minor,
                start: this.start,
                limit: this.limit,
            });
        },
    },
    watch: {
        selected: {
            handler: "changeSelected",
            deep: true,
        },
    },
    created() {
        this[types.GET_SORT_DETAIL_BOOKS]({
            gender: this.$route.params.target,
            type: this.selected.type,
            major: this.$route.params.major,
            minor: this.selected.minor,
            start: this.start,
            limit: this.limit,
        });
    },
    beforeRouteEnter(to, from, next) {
        let subSortData = JSON.parse(localStorage.getItem("sub-sort"));
        if (subSortData && subSortData.expires > Date.now()) {
            // 对数据进行处理
            let minorObj = subSortData.data[to.params.target];
            let mins = [];
            minorObj.forEach((element) => {
                if (element.major == to.params.major) {
                    mins = element.mins;
                }
            });
            mins.unshift("全部");
            let minsArr = [];
            mins.forEach((el) => {
                if (el == "全部") {
                    minsArr.push({
                        text: el,
                        active: true,
                    });
                } else {
                    minsArr.push({
                        text: el,
                        active: false,
                    });
                }
            });
            next((vm) => {
                vm.target = to.params.target;
                vm.major = to.params.major;
                vm.minor = minsArr;
            });
        } else {
            getData("http://novel.kele8.cn/sub-categories")
                .then((res) => {
                    // 对数据进行处理
                    let minorObj = res.data[to.params.target];
                    let mins = [];
                    minorObj.forEach((element) => {
                        if (element.major == to.params.major) {
                            mins = element.mins;
                        }
                    });
                    mins.unshift("全部");
                    let minsArr = [];
                    mins.forEach((el) => {
                        if (el == "全部") {
                            minsArr.push({
                                text: el,
                                active: true,
                            });
                        } else {
                            minsArr.push({
                                text: el,
                                active: false,
                            });
                        }
                    });
                    next((vm) => {
                        vm.target = to.params.target;
                        vm.major = to.params.major;
                        vm.minor = minsArr;
                    });
                    localStorage.setItem(
                        "sub-sort",
                        JSON.stringify({
                            expires: 24 * 60 * 60 * 1000,
                            data: res.data,
                        })
                    );
                })
                .catch((err) => {
                    console.log(err);
                });
        }
    },
};
</script>

<style lang="less" scoped>
@dark-gray: #b0b0b3;

.sort-detail {
    max-height: 100vh;
    overflow-y: hidden;
}
.header {
    display: flex;
    padding: 10px;
    justify-content: left;
    align-items: center;
    font-size: 22px;
    .leftIcon {
        color: #e4393c;
    }
    span {
        color: #e4393c;
        font-size: 16px;
        margin-left: 10px;
    }
}

.tags {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 15px 10px 10px;
    border-bottom: 1px dashed #eee;
    li.tag {
        font-size: 13px;
        margin-right: 10px;
        margin-bottom: 5px;
        letter-spacing: 1px;
        padding: 5px 8px;
    }
    li.active {
        background-color: #fcecec;
        color: #e4393c;
        border-radius: 40px;
    }
}
.books {
    margin-top: 10px;
    max-height: 69vh;
    overflow-y: scroll;
    .more {
        padding: 5px;
    }
}

.empty {
    padding: 10px;
    color: @dark-gray;
    font-size: 13px;
}
</style>
