<template>
    <li @click="routeTo(item._id)">
        <div class="book-img">
            <img
                class="auto-img"
                :src="'http://statics.zhuishushenqi.com' + item.cover"
            />
        </div>
        <div class="book-content">
            <div class="book-title">{{ item.title }}</div>
            <div class="book-text">
                {{
                    item.shortIntro
                        ? item.shortIntro
                        : item.longIntro != ""
                        ? item.longIntro
                        : "暂无描述..."
                }}
            </div>
            <div class="book-author">
                <div class="author-left">
                    <img
                        class="auto-img"
                        :src="require('../assets/default.png')"
                    />
                    <span class="author">{{ item.author }}</span>
                </div>
                <div class="author-right">
                    <span class="book-tag" v-if="item.minorCate">{{
                        item.minorCate
                    }}</span>
                    <span class="book-tag special">{{ followerCount }}</span>
                </div>
            </div>
        </div>
    </li>
</template>

<script>
export default {
    name: "BookCard",
    props: {
        item: Object,
    },
    computed: {
        followerCount() {
            return (this.item.latelyFollower / 10000).toFixed(1) + "万人气";
        },
    },
    methods: {
        routeTo(id) {
            this.$router.push({ name: "BookDetail", params: { id: id } });
        },
    },
};
</script>

<style lang="less" scoped>
@dark-gray: #b0b0b3;
@main-red: #e4393c;
li {
    display: flex;
    flex-grow: 50px;
    margin: 10px 10px 0;
    .book-img {
        flex: 3;
        .auto-img {
            min-width: 100%;
            width: 100%;
            vertical-align: middle;
        }
    }
    .book-content {
        flex: 9;
        margin-left: 10px;

        .book-title {
            text-align: left;
            font-weight: bold;
            padding-left: 3px;
            display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
            -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
            -webkit-line-clamp: 1; /** 显示的行数 **/
            text-overflow: ellipsis;
            overflow: hidden;
        }
        .book-text {
            margin: 10px 0;
            text-align: left;
            font-size: 12px;
            color: @dark-gray;
            text-overflow: ellipsis;
            display: -webkit-box; /** 对象作为伸缩盒子模型显示 **/
            -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
            -webkit-line-clamp: 2; /** 显示的行数 **/
            overflow: hidden; /** 隐藏超出的内容 **/
        }
        .book-author {
            display: flex;
            align-items: center;
            .author-left {
                flex: 5;
                display: flex;
                align-items: center;
                .auto-img {
                    width: 22px;
                    vertical-align: middle;
                }

                .author {
                    font-size: 12px;
                    text-align: center;
                    margin-left: 5px;
                    color: @dark-gray;
                }
            }
            .author-right {
                flex: 7;
                display: flex;
                align-items: center;
                .book-tag {
                    margin-left: 10px;
                    font-size: 10px;
                    padding: 5px 8px;
                    background-color: #f0eff4;
                    color: @dark-gray;
                    border-radius: 20px;
                }
                .special {
                    background-color: #fcecec;
                    color: @main-red;
                }
            }
        }
    }
}
</style>
